<template>
  <div class="music-player">
    <audio ref="audio" :src="currentSong.url" @timeupdate="updateTime" @loadedmetadata="updateDuration" @ended="nextSong"></audio>
    <div class="controls">
      <a @click="prevSong">⏮</a><a @click="playPause">{{ isPlaying ? '⏸' : '▶️' }}</a><a @click="nextSong">⏭</a>
    </div>
    <div class="info">
      <div>
        <span class="song-title">{{ currentSong.title }}</span><span>{{ formatTime(currentTime) }} / {{ formatTime(duration) }}</span>
      </div>

      <div class="actions">
        <button>操作</button>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      isPlaying: false,
      currentSongIndex: 0,
      currentTime: 0,
      duration: 0,
      songs: [
        {title: '歌曲的1', url: 'https://xiongod.github.io/ndwtb2022/7d17c320-1b4f-4e96-a00c-2810b87254d4.mp3'},
        {title: '歌曲2', url: 'https://xiongod.github.io/ndwtb2022/38832b78-0f51-4e08-9bca-6dc44deeca5a.mp3'},
        {title: '歌曲3', url: 'https://xiongod.github.io/ndwtb2022/5be4a985-e480-42c7-99c6-ee0d7aaff025.mp3'}
      ]
    }
  },
  computed: {
    currentSong() {
      return this.songs[this.currentSongIndex]
    }
  },
  methods: {
    playPause() {
      const audio = this.$refs.audio
      if (this.isPlaying) {
        audio.pause()
      } else {
        audio.play()
      }
      this.isPlaying = !this.isPlaying
    },
    nextSong() {
      this.currentSongIndex = (this.currentSongIndex + 1) % this.songs.length
      this.isPlaying = false
      this.playPause()
    },
    prevSong() {
      this.currentSongIndex = (this.currentSongIndex - 1 + this.songs.length) % this.songs.length
      this.isPlaying = false
      this.playPause()
    },
    updateTime() {
      this.currentTime = this.$refs.audio.currentTime
    },
    updateDuration() {
      this.duration = this.$refs.audio.duration
    },
    formatTime(seconds) {
      const minutes = Math.floor(seconds / 60)
      const secs = Math.floor(seconds % 60)
      return `${minutes}:${secs < 10 ? '0' : ''}${secs}`
    }
  }
}
</script>

