<template>
  <List border>
    <ListItem v-for="(mp3, index) in this.mp3List">
      {{index+1}}、{{mp3.title}}
      <br>
      {{mp3.duration}}
    </ListItem>

  </List>
</template>

<script>
import axios from "axios";
import {getAuthorList} from "@/api/api";

export default {
  data () {
    return {
      mp3List:[]
    }
  },


  async mounted() {
    const url =this.$route.query.url
    const response = await axios.get(url);
    //console.log(response.data)
    this.mp3List = response.data
  }
}
</script>
